/*楼盘画报*/
.building-poster {
  width: 100%;
  margin-bottom: 1rem;
}
.building-poster > .poster-info {
  padding: 1.85rem 1.5rem 2rem 1.5rem;
}
.building-poster > .poster-info > .title {
  font-size: 1.6rem;
  color: #4e4e4e;
  letter-spacing: 0;
  line-height: 2rem;
}
.building-poster > .poster-info > .time {
  font-size: 1.3rem;
  color: #C3C3C3;
  letter-spacing: 0;
  line-height: 1.2rem;
  margin-bottom: 1.5rem;
}
.building-poster > .poster-info > .poster-pic {
  width: 100%;
  height: 18.8rem;
}
.building-poster > .poster-info > .poster-pic > img {
  max-width: 100%;
  height: auto;
}
.building-poster > .poster-info > .poster-content {
  font-size: 1.6rem;
  color: #999;
  letter-spacing: 0;
  line-height: 2.35rem;
  margin-top: 1rem;
}



