/*//form表单元素*/
.xf-status > .base {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #fff;
}
.xf-status > .building-status {
  z-index: 2;
  width: 100%;
  padding: 0 1.5rem 1.5rem;
}
.xf-status > .building-status > .single-status:after {
  position: absolute;
  bottom: 0;
  content: '';
  width: 100%;
  height: 1px;
  border-top: 1px solid #ddd;
  -webkit-transform: scaleY(0.5);
}
.xf-status > .building-status > .single-status {
  position: relative;
  padding-bottom: 2rem;
  margin-top: 1.5rem;
}
.xf-status > .building-status > .single-status > h3 {
  font-size: 1.4rem;
  color: #4e4e4e;
  letter-spacing: 0;
  margin-bottom: 1rem;
}
.xf-status > .building-status > .single-status > p.content {
  font-size: 1.4rem;
  color: #4e4e4e;
  letter-spacing: 0;
  line-height: 2.1rem;
}
.xf-status > .building-status > .single-status > .time {
  font-size: 1.2rem;
  color: #999;
  letter-spacing: 0;
  margin-top: 1rem;
}



