.comments-list .comment-list-info[data-v-5693f98c] {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}
.comments-list .comment-list-info .panel-item[data-v-5693f98c] {
  border-bottom: 1px solid #E4E4E4;
}
.comments-list .comment-list-info .comment-phone[data-v-5693f98c] {
  position: relative;
}
.comments-list .comment-list-info .comment-phone img[data-v-5693f98c] {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  margin-right: .5rem;
}
.comments-list .comment-list-info .comment-phone span[data-v-5693f98c] {
  margin-right: 0.85rem;
}
.comments-list .comment-list-info .comment-phone i[data-v-5693f98c] {
  color: #f95355;
}
.comments-list .comment-list-info .comment-phone .yezhu[data-v-5693f98c] {
  color: #4081d6;
}
.comments-list .comment-list-info .comment-time-like[data-v-5693f98c]:after {
  clear: both ;
  content: " ";
  display: block ;
  font-size: 0 ;
  height: 0 ;
  visibility: hidden ;
}
.comments-list .comment-list-info .comment-time-like .click-like[data-v-5693f98c] {
  float: right;
}
.comments-list .comment-list-info .comment-time-like .isUp[data-v-5693f98c] {
  color: #4081d6;
}
.comments-list .comment-list-info .comment-time-like .isUp + .comment-like-amount[data-v-5693f98c] {
  color: #4081d6;
}
.comments-list .comment-list-info ul[data-v-5693f98c] {
  padding: 1rem 0;
}
.comments-list .comment-list-info ul[data-v-5693f98c]:after {
  clear: both ;
  content: " ";
  display: block ;
  font-size: 0 ;
  height: 0 ;
  visibility: hidden ;
}
.comments-list .comment-list-info ul li[data-v-5693f98c] {
  float: left;
  margin-right: 3.5%;
  overflow: hidden;
  width: 31% ;
}
.comments-list .comment-list-info ul li[data-v-5693f98c]:last-child {
  margin-right: 0;
}
.comments-list .comment-all-info:last-child .panel-item[data-v-5693f98c] {
  border: none;
}



