/*用户评论*/
.xf-comments > .base {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #fff;
}
.xf-comments .comments {
  width: 100%;
}
.xf-comments .comments > .comments-detail {
  padding: 0 1.5rem 2rem 1.5rem;
}
.xf-comments .comments > .comments-detail > .first-comment:after {
  position: absolute;
  bottom: 0;
  left: 0;
  content: '';
  width: 100%;
  height: 1px;
  border-top: 1px solid #ddd;
  -webkit-transform: scaleY(0.5);
}
.xf-comments .comments > .comments-detail > .single-comment {
  margin-top: 2rem;
  padding-bottom: 1.5rem;
  position: relative;
}
.xf-comments .comments > .comments-detail > .single-comment > .comments-top > img {
  width: 3rem;
  height: 3rem;
}
.xf-comments .comments > .comments-detail > .single-comment > .comments-top > span {
  font-size: 1.6rem;
  color: #999;
  display: inline-block;
  margin-right: 0.85rem;
}
.xf-comments .comments > .comments-detail > .single-comment > .comments-top > .icon-youpingsvg {
  color: #f95355;
}
.xf-comments .comments > .comments-detail > .single-comment > .comments-top > .icon-yezhu {
  color: #4081d6;
}
.xf-comments .comments > .comments-detail > .single-comment > .comment-content {
  font-size: 1.6rem;
  color: #4e4e4e;
  line-height: 2.6rem;
}
.xf-comments .comments > .comments-detail > .single-comment > .time {
  font-size: 1.4rem;
  color: #999;
  letter-spacing: 0;
  clear: both;
}
.xf-comments .comments > .comments-detail > .single-comment > .time > span {
  float: right;
}
.xf-comments .comments > .comments-detail > .single-comment > .time > span > i {
  margin-right: 0.2rem;
}



