.guest-comment .comment[data-v-2ae87610] {
  margin-left: 0;
  padding: 1rem 1.5rem;
}
.guest-comment .comment[data-v-2ae87610]:after {
  clear: both ;
  content: " ";
  display: block ;
  font-size: 0 ;
  height: 0 ;
  visibility: hidden ;
}
.guest-comment .comment .comment-amount[data-v-2ae87610] {
  float: right;
}
.guest-comment .comment .comment-amount .skip[data-v-2ae87610] {
  color: #4e4e4e;
}
.guest-comment .no-data[data-v-2ae87610] {
  font-size: 1.6rem;
  color: #999;
  text-align: center;
  line-height: 3rem;
}
.guest-comment .comment-all-info[data-v-2ae87610] {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}
.guest-comment .comment-all-info .panel-item[data-v-2ae87610] {
  border-bottom: 1px solid #E4E4E4;
}
.guest-comment .comment-all-info .comment-phone[data-v-2ae87610] {
  position: relative;
}
.guest-comment .comment-all-info .comment-phone img[data-v-2ae87610] {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  margin-right: .5rem;
}
.guest-comment .comment-all-info .comment-phone span[data-v-2ae87610] {
  margin-right: 0.85rem;
}
.guest-comment .comment-all-info .comment-phone i[data-v-2ae87610] {
  color: #f95355;
}
.guest-comment .comment-all-info .comment-time-like[data-v-2ae87610]:after {
  clear: both ;
  content: " ";
  display: block ;
  font-size: 0 ;
  height: 0 ;
  visibility: hidden ;
}
.guest-comment .comment-all-info .comment-time-like .click-like[data-v-2ae87610] {
  float: right;
}
.guest-comment .all-comment ul[data-v-2ae87610] {
  padding: 1rem 0;
}
.guest-comment .all-comment ul[data-v-2ae87610]:after {
  clear: both ;
  content: " ";
  display: block ;
  font-size: 0 ;
  height: 0 ;
  visibility: hidden ;
}
.guest-comment .all-comment ul li[data-v-2ae87610] {
  float: left;
  margin-right: 3.5%;
  overflow: hidden;
  width: 31% ;
}
.guest-comment .all-comment ul li[data-v-2ae87610]:last-child {
  margin-right: 0;
}
.guest-comment .all-comment .comment-all-info:last-child .panel-item[data-v-2ae87610] {
  border: none;
}



