.xf-info {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}
.xf-info > .base {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #fff;
  z-index: -1;
}
.xf-info > .info {
  width: 100%;
  padding-bottom: 1.5rem;
  position: relative;
}
.xf-info > .info > p {
  font-size: 1.6rem;
  color: #999;
  clear: both;
  margin-top: 2rem;
}
.xf-info > .info > p > span {
  float: right;
  display: inline-block;
  color: #4e4e4e;
}
.xf-info > .info-line:after {
  position: absolute;
  bottom: 0;
  content: '';
  height: 1px;
  width: 100%;
  border-top: 1px solid #ddd;
  -webkit-transform: scaleY(0.5);
}



