.xf-poster-preview > .base {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #fff;
}
.xf-poster-preview > .swiper-container .swiper-slide {
  position: relative ;
}
.xf-poster-preview > .swiper-container .swiper-slide > .pagination-num {
  height: 5.4rem;
  width: 100%;
  padding-right: 1.5rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
  -ms-flex-pack: end;
  justify-content: flex-end;
  -webkit-box-align: Center;
  -ms-flex-align: Center;
  align-items: Center;
}
.xf-poster-preview > .swiper-container .swiper-slide > .pagination-num > .pagination {
  display: inline-block;
  border-radius: 100px;
  background-color: #f5f5f5;
  color: #4e4e4e;
  font-size: 1.2rem ;
  height: 2.4rem;
  width: 6.25rem;
  line-height: 2.4rem;
  text-align: center;
  z-index: 2000 ;
}
.xf-poster-preview > .swiper-container .swiper-slide > .videoImg {
  width: 100%;
  height: 37.8rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: Center;
  -ms-flex-align: Center;
  align-items: Center;
}
.xf-poster-preview > .swiper-container .swiper-slide > .description {
  padding: 2rem 1.5rem;
}
.xf-poster-preview > .swiper-container .swiper-slide > .description > h3 {
  font-size: 1.6rem;
  color: #4e4e4e;
  letter-spacing: 0;
}



